$basic: #30363A;
$brown: #C78E66;

body {
  font-family: 'Montserrat', sans-serif;
  font-size: 18px;
  line-height: calc(32 / 18);
  font-weight: 400;
  color: $basic; }

.container {
  margin: 0 auto;
  padding: 0 20px; }

.btn {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 11px;
  font-weight: 600;
  line-height: 18px;
  letter-spacing: 2px;
  text-align: center;
  height: 50px;
  max-width: 100%;
  text-transform: uppercase;
  padding-right: 27px;
  position: relative;
  background: none;
  &::before {
    @include p;
    top: calc(50% - 6px);
    right: 33px;
    width: 13px;
    height: 13px;
    border-bottom: 1px solid #c78e66;
    border-right: 1px solid #c78e66;
    transform: rotate(-45deg); }
  &_light {
    border: 1px solid #FFFFFF;
    color: #FFFFFF;
    &:hover {
      // background-color: #fff
 } }      // color: $brown
  &_dark {
    border: 1px solid $basic;
    color: $basic;
    &:hover {
      // background-color: $basic
 } }      // color: $brown
  &:hover {
    background: $brown;
    border-color: $brown;
    color: #fff;
    &::before {
      border-color: #fff; } } }

.header {
  background: $basic;
  box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.1);
  color: #fff;
  padding: 24px 40px;
  &.stickytop {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 20; }
  &-links {
    &__item {
      padding: 0 21px;
      position: relative;
      &::before {
        @include p;
        width: 1px;
        height: 30px;
        left: 0;
        top: -2px;
        background: #FFFFFF;
        opacity: 0.2; }
      &:last-child {
        padding-right: 0; } } }
  &__link {
    width: 18px;
    height: 26px;
    display: block;
    svg {
      width: 18px;
      height: 18px;
      fill: #fff;
      @include tr; }
    &:hover {
      svg {
        fill: $brown; } }
    &:active {
      svg {
        fill: #7D8082; } } }

  &-mobile {
    background: $basic;
    display: none;
    &.stickytop {
      position: fixed;
      top: 0;
      left: 0;
      right: 0;
      z-index: 20; }
    &__top {
      padding: 15px 20px;
      border-bottom: 1px solid $brown;
      position: relative; }
    &__middle {
      padding: 15px 20px;
      display: none;
      border-bottom: 1px solid $brown; }
    &__bottom {
      padding: 12.5px 20px; }
    .logo {
      width: 128px;
      margin: 0; }
    .phone {
      margin: 0; }
    .tagline {
      padding: 0;
      margin: 0;
      color: #fff;
      max-width: none;
      &::before {
        content: none; } }
    .menu {
      &-items {
        display: block;
        text-align: right; }
      &__item {
        margin: 0; } } } }

.logo {
  margin-right: 40px;
  &__link {
    display: block;
    line-height: 0; }
  &__image {
    width: 128px; } }

.tagline {
  font-size: 12px;
  line-height: 15px;
  letter-spacing: 1px;
  text-transform: uppercase;
  max-width: 160px;
  padding-left: 40px;
  position: relative;
  margin-right: 60px;
  &::before {
    @include p;
    width: 1px;
    height: 40px;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
    background: $brown; } }

.menu {
  &-items {}
  &__item {
    margin-right: 30px;
    &:last-child {
      margin-right: 0; } }
  &__link {
    font-size: 12px;
    font-weight: 600;
    line-height: 14px;
    letter-spacing: 1px;
    color: #fff;
    text-transform: uppercase;
    border-bottom: 1px solid transparent;
    &:hover {
      color: $brown;
      border-bottom: 1px solid $brown; }
    &:active {
      color: #7D8082;
      border-bottom-color: transparent; } } }

.phone {
  font-size: 20px;
  line-height: 1;
  letter-spacing: 1px;
  color: #fff;
  margin: 0 20px 0 auto;
  &:hover {
    color: $brown; }
  &__image {
    display: none; } }

.intro {
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  min-height: 1000px;
  padding: 108px 0 0;
  position: relative;
  &__header {
    font-size: 30px;
    line-height: 1;
    letter-spacing: 2px;
    text-transform: uppercase;
    text-align: center;
    font-weight: 400;
    &_light {
      color: #fff; }
    span {
      display: block;
      font-size: 80px;
      font-weight: 300;
      line-height: 1;
      letter-spacing: 0px;
      margin-top: 10px; } }
  &__goto {
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    height: 60px;
    width: 60px;
    background: $basic url('../img/sprite.svg#arrow-down') no-repeat center;
    background-size: 20px;
    &:hover {
      background-color: #fff; } } }

.models {
  padding: 113px 0 112px;
  &-items {
    display: grid;
    grid-gap: 40px;
    grid-template-columns: repeat(2, 1fr); }
  &__item {
    text-align: center;
    &-name {
      font-size: 80px;
      font-weight: 300;
      line-height: 1;
      text-transform: uppercase;
      margin-bottom: 20px; }
    &-type {
      font-size: 20px;
      font-weight: 400;
      line-height: 1;
      letter-spacing: 1px;
      text-transform: uppercase;
      margin-bottom: 19px; }
    &-image {
      display: block;
      margin: 0 auto 20px; } }
  &-props {
    display: grid;
    grid-row-gap: 40px;
    grid-column-gap: 20px;
    grid-template-columns: repeat(2, 1fr);
    padding: 0 40px; }
  &__prop {
    position: relative;
    padding-top: 31px;
    &::before {
      @include p;
      width: 20px;
      height: 20px;
      background-repeat: no-repeat;
      background-position: center;
      background-size: 20px;
      top: 0;
      left: 50%;
      transform: translateX(-50%); }
    &::after {
      @include p;
      width: 1px;
      height: 138px;
      background: #DEDEDE;
      top: 50%;
      transform: translateY(-50%);
      right: -10px; }
    &:nth-child(even) {
      &::after {
        content: none; } }
    &-power {
      &::before {
        background-image: url('../img/sprite.svg#power'); } }
    &-torque {
      &::before {
        background-image: url('../img/sprite.svg#torque'); } }
    &-racing {
      &::before {
        background-image: url('../img/sprite.svg#racing'); } }
    &-fuel {
      &::before {
        background-image: url('../img/sprite.svg#fuel'); } }
    &-value {
      font-size: 80px;
      font-weight: 300;
      line-height: 1;
      margin-bottom: 9px; }
    &-name {
      font-size: 18px;
      font-weight: 400;
      line-height: 1; } } }

.subheader {
  font-size: 20px;
  line-height: 1;
  letter-spacing: 1px;
  text-transform: uppercase;
  color: #CACACA; }

h2 {
